.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  flex-direction: row;
  gap: 30px;
  padding-top: 20px;
  display: flex;
  position: relative;
}

.cat-text {
  width: 600px;
  flex-direction: column;
  display: flex;
}

